<template>
  <div class="writeBox">
    <div class="main">
      <div class="title">
        <span>标题</span>
        <Input v-model="updata.title" />
      </div>
      <div class="text">
        <span>正文</span>
        <Input v-model="updata.text" type="textarea" />
      </div>
      <div class="button">
        <Button @click="setnull">清空</Button>
        <Button @click="upload" type="primary" class="upload">上传</Button>
      </div>
    </div>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      updata: {
        title: "",
        text: "",
        uploadTime:""
      },
    };
  },
  methods: {
    upload() {
      console.log("updata", this.updata);
      let that = this;
      axios
        .post("http://127.0.0.1:7001/uploadData", this.updata)
        .then(function (res) {
          // console.log(res.data);
          if (res.data.code == 1) {
            that.setnull();
            that.$Message.success(res.data.msg);
          } else {
            that.$Message.error(res.data.msg);
          }
        });
    },
    setnull() {
      this.updata = {
        title: "",
        text: "",
      };
    },
 
  },
};
</script>

<style scoped lang="scss">
.writeBox {
  width: 100%;
  height: 100%;
  .main {
    width: 50%;
    height: 550px;
    border: 1px solid #dcdee2;
    border-radius: 8px;
    padding: 20px;
    background-color: #e9eaef;
    .title {
      display: flex;
      align-items: center;
      justify-content: center;
      margin-bottom: 20px;
      span {
        width: 50px;
      }
    }
    .text {
      display: flex;
      margin-bottom: 20px;
      span {
        width: 50px;
      }
    }
    .button {
      display: flex;
      justify-content: flex-end;
      .upload {
        margin-left: 10px;
      }
    }
  }
}
</style>

<style scoped lang="scss">
::v-deep .text .ivu-input {
  height: 400px !important;
}
</style>