<template>
  <div class="box">
    <Tabs @on-click="changeTab" :value="tabName">
      <TabPane label="内容" icon="ios-paper" name="page">
        <page style="height:100%;" ref="page"></page>
      </TabPane>
      <TabPane label="说说" icon="md-create" name="write">
        <write style="height:100%;"></write>
      </TabPane>
    </Tabs>
  </div>
</template>

<script>
import write from "./write/index.vue";
import page from "./page/index.vue";
export default {
  components: {
    write,
    page
  },
  data() {
    return {
        tabName:"page"
    };
  },
  methods:{
     changeTab(name){
         if(name == "page"){
          this.$refs.page.getpageData()
         }
         
     } 
  },
  mounted(){
      this.$refs.page.getpageData()
  }
};
</script>

<style scoped lang="scss">
.box{
    height: 100%;
    width:100%;
    padding: 10px;
    // background-color: pink;
}
</style>